"use strict";
var angular_1 = require('angular');
var home_component_1 = require('./home.component');
function routeConfig($stateProvider) {
    "ngInject";
    $stateProvider
        .state('app.home', {
        url: '/',
        component: 'home'
    });
}
function runConfig() {
}
exports.Home = angular_1.module('components.home', [])
    .component('home', new home_component_1.HomeComponent)
    .config(routeConfig)
    .run(runConfig)
    .name;



